<template>
  <v-app>
    <v-main>
      <!-- App Bar -->
      <v-app-bar app dark class="app-bar-bg px-4 d-flex align-center">
        <div class="d-flex align-center">
          <v-app-bar-nav-icon
            variant="text"
            class="text-white"
            @click.stop="drawer = !drawer"
          />
          <v-toolbar-title class="text-white font-weight-bold ml-4 mb-0">
            exships
          </v-toolbar-title>
        </div>
      </v-app-bar>

      <!-- Navigation Drawer -->
      <v-navigation-drawer v-model="drawer">
        <v-list>
          <v-list-item
            v-for="item in navItems"
            :key="item.value"
            :prepend-icon="item.icon"
            :title="item.title"
            :to="item.to"
            @click="handleNavClick(item)"
          />
        </v-list>
      </v-navigation-drawer>

      <!-- 🌌 Animated Hero Section -->
      <section class="hero-section" ref="hero">
        <div class="stars"></div>
        <div class="twinkling"></div>
        <div class="ocean"></div>

        <div class="hero-content" :class="{ visible: heroVisible }">
          <transition name="fade-slide" appear>
            <div v-if="heroVisible">
              <h1>ITで「真の価値」を届ける</h1>
              <p>合同会社エクスシップス — exships LLC.</p>
              <v-btn
                color="indigo"
                variant="flat"
                size="large"
                class="mt-6"
                @click="scrollToFeatures"
              >
                強みを見る
              </v-btn>
            </div>
          </transition>
        </div>
      </section>

      <!-- 💼 Features Section -->
      <v-container class="mt-16 mb-16" ref="features">
        <v-row>
          <v-col
            v-for="(feature, i) in features"
            :key="i"
            cols="12"
            md="4"
            class="text-center"
          >
            <transition name="fade-up" appear>
              <v-card elevation="6" class="pa-6 hover-card">
                <v-icon size="48" color="primary" class="mb-4">
                  {{ feature.icon }}
                </v-icon>
                <h3 class="font-weight-bold mb-2">{{ feature.title }}</h3>
                <p class="text-medium-emphasis">{{ feature.description }}</p>
              </v-card>
            </transition>
          </v-col>
        </v-row>
      </v-container>

      <!-- Footer -->
      <v-footer class="v-footer">
        <v-container class="text-center white--text">
          © 2020 - 2025 exships LLC.
        </v-container>
      </v-footer>
    </v-main>
  </v-app>
</template>

<script>
export default {
  data: () => ({
    drawer: null,
    heroVisible: false,
    navItems: [
      { icon: "mdi-ferry", title: "Home", value: "home", to: "/" },
      {
        icon: "mdi-message",
        title: "Message",
        value: "message",
        to: "/message",
      },
      {
        icon: "mdi-domain",
        title: "Company",
        value: "company",
        to: "/company",
      },
      {
        icon: "mdi-view-list-outline",
        title: "Projects",
        value: "projects",
        to: "/projects",
      },
      { icon: "mdi-forum", title: "Contact", value: "contact", to: "/contact" },
      // { icon: "mdi-debug", title: "Debug", value: "debug", to: "/debug" },
      // {
      //   icon: "mdi-badge-account-horizontal-outline",
      //   title: "About",
      //   value: "about",
      //   to: "/about",
      // },
    ],
    features: [
      {
        icon: "mdi-cellphone-link",
        title: "クロスプラットフォーム開発",
        description:
          "iOS / Android / Web を横断し、高品質なアプリケーションを効率的に開発します。",
      },
      {
        icon: "mdi-brain",
        title: "AI・機械学習",
        description:
          "AI・ML技術を活用し、よりスマートで自律的なサービスを実現します。",
      },
      {
        icon: "mdi-chart-bubble",
        title: "チーム開発支援",
        description:
          "アジャイルとスクラムで、チームの力を最大化する仕組みを構築します。",
      },
    ],
  }),
  mounted() {
    setTimeout(() => (this.heroVisible = true), 600);
  },
  methods: {
    hideDrawer: function () {
      this.drawer = false;
    },
    handleNavClick(item) {
      // 現在のページと同じなら閉じる
      if (this.$route.path === item.to) {
        this.hideDrawer();
      }
    },
    scrollToFeatures() {
      this.$nextTick(() => {
        const el = this.$refs.features?.$el; // Vuetify コンポーネントの DOM
        if (el && el.scrollIntoView) {
          el.scrollIntoView({ behavior: "smooth" });
        } else {
          console.warn(
            "Features element not found or scrollIntoView unavailable"
          );
        }
      });
    },
  },
};
</script>

